import ajax from '@/services/ajax';
import api from '@/services/api';

export default {

    // 
    getKeepRate: data => {
    	return ajax.postAfterLoginQuery('017407', data, api.getKeepRate);
    },
    /*  */
    // 
    submitLive2keep: data => {
    	return ajax.postAfterLoginUpdate('ZCZQ', data, api.submitLive2keep);
    },
    // 
    submitLive2keepLive: data => {
        return ajax.postAfterLoginUpdate('DHLB', data, api.submitLive2keepLive);
    },
    // 
    successLive2keepLive: data => {
        return ajax.post(api.successLive2keepLive, data);
    },
    // 
    submitLive2keepKeep: data => {
        return ajax.post(api.submitLive2keepKeep, data);
    },
    // 
    successLive2keepKeep: data => {
        return ajax.post(api.successLive2keepKeep, data);
    },
    /*  */
    // 
    getKeepListForAccount: data => {
        return ajax.post(api.getKeepListForAccount, data);
    },
    // 
    getKeepDetail: data => {
        return ajax.post(api.getKeepDetail, data);
    },
    // 
    submitKeep2live: data => {
        return ajax.post(api.submitKeep2live, data);
    },
    // 
    successKeep2live: data => {
        return ajax.post(api.successKeep2live, data);
    },
    /*  */
    // 
    getKeepSearchListForAccount: data => {
        return ajax.postAfterLoginQueryList('017402', data, api.getKeepSearchListForAccount);
    },
    // 
    getKeepSearchDetail: data => {
        return ajax.postAfterLoginQuery('', data, api.getKeepSearchDetail);
    },
    // 
    getKeepSearchResult: data => {
        return ajax.postAfterLoginQuery('017405', data, api.getKeepSearchResult);
    }
};



